@import url(https://fonts.googleapis.com/css2?family=Baloo+Thambi+2&family=Bebas+Neue&display=swap);
nav {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 4em;
    background-color: gray;
    list-style: none;
    border-bottom: 0.2em solid black;
    color: white;
}

img {
    width: 3em;
    height: 2.5em;
}

li {
    padding: 2em;
    font-size: 1.2em;
}

.cartDiv {
    display: flex;
    align-items: center;
    justify-content: center;
}

.cartDiv > span {
    position: relative;
    right: 0.8em;
    font-size: 1.2em;
    padding-left: 0.2em;
}

a {
    text-decoration: none;
    transition: all 0.9s ease;
    color: white;
}

a:hover {
    font-size: 1.05em;
    letter-spacing: 0.1em;
}

a:visited {
    color: white;
}

.cart {
    position: absolute;
    right: 0;
    transition: all 0.9s ease;
    padding: 0;
}

.cart:hover .cartDiv span {
    transform: scale(1.05);
}
.item {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 12em;
    height: 18em;
    text-align: center;
    border: 0.15em solid black;
    border-radius: 5px;
    font-family: 'Baloo Thambi 2', cursive;
    background-color: white;
}

.nameLabShop {
    height: 2.5em;
    width: 100%;
}

.priceLabShop {
    font-weight: bold;
}

.item > img {
    width: 100%;
    height: 50%;
}

.item > button {
    width: 80%;
    height: 2em;
    border: 0.2em solid black;
    border-radius: 5px;
    background-color: transparent;
    font-weight: bolder;
    transition: all 0.6s ease;
}

.item > button:hover {
    transform: scale(1.1);
}
.loader {
    margin: auto;
    margin-top: 20%;
    border: 1em solid #f3f3f3;
    border-top: 1em solid #3498db;
    border-radius: 50%;
    width: 3em;
    height: 3em;
    animation: spin 2s linear infinite;
}

@keyframes spin {
    0% {transform: rotate(0deg);}
    100% {transform: rotate(360deg);}
}



.shopMain {
    background-image: url(/Shopping-cart/static/media/back2.4d38b583.jpg);
    height: 100%;
    margin: 0;
    padding-top: 1.5em;
    padding-bottom: 1.5em;
    color: black;
}

.shopDiv {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding-top: 1.5em;
    padding-bottom: 1.5em;
    border: 0.2em solid black;
    border-radius: 5px;
    width: 60vw;
    min-height: 50vh;
    background-color: rgb(247, 247, 247, 0.9); 
    animation: fadeIn 1.2s ease backwards;
}

.itemsDiv {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(10, 1fr);
    grid-column-gap: 10vw;
    column-gap: 10vw;
    grid-row-gap: 4em;
    row-gap: 4em;
}

@keyframes fadeIn {
    0% {
        opacity: 0;  
    }
    100% {
        opacity: 1;
    }
}


.homeContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
}

.welcome {
    position: relative;
    font-size: 5em;
    opacity: 1;
    animation: welcome-slide 0.8s ease-in-out,
                op 0.8s ease-in;
}

.quote {
    position: relative;
    font-size: 2.5em;
    animation: op 1.2s ease-in 0.5s backwards;
}

@keyframes welcome-slide {
    0% {
        left: -1%;
        opacity: 0;
        
    }
    100% {
        left: 0;
        opacity: 1;
       
    }
}

@keyframes op {
    0% {opacity: 0;}
    100% {opacity: 1;}
}

.aboutContent {
    position: relative;
    margin: auto;
    top: 20vh;;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60vw;
    text-align: left;
}

.aboutContent p {
    position: relative;
    font-size: 1.5em;
    animation: fadeIn 0.8s ease 0.3s backwards;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        left: -1%
    }
    100% {
        opacity: 1;
        left: 0;
    }
}
.cartItem {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 40em;
    margin-bottom: 0.8em;
    border-bottom: 0.1em solid black;
    font-family: 'Baloo Thambi 2', cursive;
}

.cartItem > img {
    width: 6em;
    height: 5em;
}

.cartItem > * {
    padding: 0.5em;
}

.valueDiv > input {
    width: 2.5em;
}

.valueDiv {
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 0.4em;
    margin-right: 1em;
}

.nameAndPriceDiv {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 33%;
}

.nameLab {
    width: 6em;
}

.priceLab {
    width: 3em;
    font-weight: bold;
}


.btnsDiv {
    display: flex;
    align-items: center;
    width: 33%;
}

.plusBtn, .minusBtn {
    border: 0;
    background-color: transparent;
    height: 1.5em;
    width: 1.5em;
    transition: all 0.7s ease
}

.plusBtn:hover, .minusBtn:hover {
    transform: scale(1.1);
}

.deleteBtn {
    height: 2.3em;
    width: 2.3em;
    transition: all 0.7s ease
}

.deleteBtn:hover {
    transform: scale(1.1);
}
.total {
    font-size: 3em;
    margin: 0;
}

.buyBtn {
    width: 90%;
    height: 2.8em;
    margin-top: 0.5em;
    color: white;
    background-color: blue;
    border:  solid blue;
    border-radius: 10px;
    font-size: 2em;
    font-weight: bolder;
    transition: all 0.7s ease;
}

.buyBtn:hover {
    transform: scale(1.1);
}

.cashout {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.cartItems {
    margin-top: 1.5em;
    display:flex;
    flex-direction: column;
    align-items: center;
    animation: fadeIn 1s ease backwards;
}

.emptyCart {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 3em;
    
}

.emptyCart h1 {
    font-size: 6em;
}

@keyframes fadeIn {
    0% {
        opacity: 0;  
    }
    100% {
        opacity: 1;
    }
}

.shopBtn {
    width: 8em;
    height: 3em;
    border: 0.2em solid black;
    border-radius: 5px;
    background-color: transparent;
    font-weight: bolder;
}

.checkoutDiv {
    margin-top: 6em;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.checkoutDiv > h1 {
    position: relative;
    font-size: 5em;
    animation: fadeIn 1.3s ease 0.3s backwards;
}

button {
    width: 7em;
    height: 3em;
    border: 0.2em solid black;
    border-radius: 5px;
    background-color: transparent;
    font-weight: bolder;
    transition: all 0.6s ease;
}

button:hover {
    transform: scale(1.1)
}

@keyframes fadeIn {
    0% {
        left: -1%;
        opacity: 0;}
    100% {
        left: 0;
        opacity: 1;}
}
body, html {
    margin: 0;
    padding: 0;
    font-size: 16px;
    font-family: 'Bebas Neue', cursive;
    background-image: url(/Shopping-cart/static/media/back1.b1da9774.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    color: white;
}

